import { Card, Typography } from '@material-ui/core'
import * as React from 'react'
import { connect } from 'react-redux'
import { SingleSelectField } from '@src/components/SingleSelectField'
import { Tree } from '@src/components/Tree'
import { ICategory } from '@src/__generated__/backendSchemas'

interface StateProps {
  categories: Array<ICategory>
  iconChoices: Array<{
    name: string
    value: string
  }>
  CategoryTree: {}
}

type CategoryListProps = StateProps

class CategoryList extends React.Component<CategoryListProps> {
  render() {
    return (
      <div>
        <Tree root={{ id: '1', children: [] }} />
        <div>{this.props.categories.map(cat => this.getCard(cat))}</div>
      </div>
    )
  }
  onSelectIcon(category: ICategory, changes) {
    /*
    return this.props.actions.CategoryUpdate({
      id: category.id,
      mui_icon_id: changes.mui_icon_id,
    })
     */
  }

  getCard(cat: ICategory) {
    const choices = this.props.iconChoices.map(i => {
      return {
        value: i.name,
        mui_icon_id: i.value,
        label: i.name,
      }
    })
    return (
      <Card key={cat.id}>
        <Typography style={{ display: 'flex' }}>{cat.description}</Typography>
        <SingleSelectField
          style={{ flex: 1 }}
          instance={cat}
          choices={choices}
          instancePropNameToUpdate="mui_icon_id"
          onChange={this.onSelectIcon.bind(this)}
        />
      </Card>
    )
  }
}

const mapStateToProps = (state): StateProps => {
  return {
    categories: state.categories,
    iconChoices: [],
    CategoryTree: {},
  }
}
const mapDispatchToProps = dispatch => {
  return {}
}
export const CategoryListPage = connect(
  mapStateToProps,
  mapDispatchToProps,
)(CategoryList)
