import { Button, Grid } from '@material-ui/core'
import * as React from 'react'
import { getSelectableCategories, getVisibleTransactions } from '../selectors'

import { NewPlaidForm } from './NewPlaidForm'
import { NewStatementForm } from './NewStatementForm'
import { NewTransactionForm } from './NewTransactionForm'
import { TransactionListAppBar } from './TransactionListAppBar'
import { TransactionRow } from './TransactionRow'
import { IUserTransaction, ICategory } from '@src/__generated__/backendSchemas'
import { Timeline } from '@src/Root/Timeline'
import { ButlerProps, connectButler } from '@src/utils'
import { State as AppState } from '@src/types'

interface OwnProps {}
interface PropsFromStore {
  transactions: Array<IUserTransaction>
  selectableCategories: Array<ICategory>
}
const PAGE_SIZE = 40

type Prop = OwnProps & ButlerProps & PropsFromStore

type FormType = 'transaction' | 'plaid' | 'statement' | 'wave'

interface State {
  expandedTransactionId: number | null
  shownForm: FormType | null
  showTimeline: boolean
}

class TransactionListContainer extends React.Component<Prop, State> {
  state: State = {
    expandedTransactionId: null,
    shownForm: null,
    showTimeline: true,
  }

  toggleTimeline = () => this.setState({ showTimeline: !this.state.showTimeline })
  loadMoreTransactions = () => {
    const start = this.props.transactions.length
    const end = start + PAGE_SIZE
    this.props.butler.ListTransactions({ start, end }, 'merge-append')
  }
  showForm = (formType: FormType) => {
    this.setState({ shownForm: formType })
  }

  componentDidMount() {
    if (!this.props.selectableCategories.length) {
      this.props.butler.ListCategories({}, 'replace')
    }
    this.props.butler.GetUserBalance({}, 'replace')
    this.loadMoreTransactions()
  }
  onToggleExpansion(id: number) {
    if (id === this.state.expandedTransactionId) {
      this.setState({ expandedTransactionId: null })
    } else {
      this.setState({ expandedTransactionId: id })
    }
  }
  closeForm = () => this.setState({ shownForm: null })

  render() {
    const { selectableCategories } = this.props
    const { showTimeline } = this.state
    if (!selectableCategories || !selectableCategories.length) {
      return null
    }
    return (
      <div style={{ maxWidth: '100%' }}>
        <TransactionListAppBar toggleTimeline={this.toggleTimeline} showForm={this.showForm} />
        <div
          style={{
            height: window.innerHeight - 70,
            overflow: 'scroll',
          }}
        >
          <Grid container>
            {showTimeline ? (
              <Grid item sm={3}>
                <Timeline />
              </Grid>
            ) : null}
            <Grid item sm={showTimeline ? 9 : 12}>
              <div style={{ marginBottom: '20px' }}>
                {this.state.shownForm === 'transaction' ? (
                  <NewTransactionForm onClose={this.closeForm} />
                ) : null}
                {this.state.shownForm === 'statement' ? (
                  <NewStatementForm onClose={this.closeForm} />
                ) : null}
                {this.state.shownForm === 'plaid' ? <NewPlaidForm origin="plaid" /> : null}
                {this.state.shownForm === 'wave' ? <NewPlaidForm origin="wave" /> : null}
              </div>
              {this.props.transactions.map(t => (
                <TransactionRow
                  expanded={this.state.expandedTransactionId === t.id}
                  key={t.id}
                  transaction={t}
                  onToggleExpansion={(event, expanded) => this.onToggleExpansion(t.id)}
                />
              ))}
              <Button fullWidth size="large" color="secondary" onClick={this.loadMoreTransactions}>
                ({this.props.transactions.length}) Click to Load More ...
              </Button>
            </Grid>
          </Grid>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state: AppState): PropsFromStore => {
  return {
    transactions: getVisibleTransactions(state),
    selectableCategories: getSelectableCategories(state),
  }
}

export const TransactionList = connectButler(mapStateToProps)(TransactionListContainer)
