import {
  AppBar,
  Icon,
  IconButton,
  ListItemIcon,
  ListItemText,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from '@material-ui/core'
import { Add, AttachMoney, CreditCard, GridOn, History, Waves } from '@material-ui/icons'
import * as React from 'react'
import { connect } from 'react-redux'
import { IconMenu } from 'rice-instructor'
import { State as AppState } from '@src/types'
import { SearchBar } from '@src/Root/SearchBar'

interface OwnProps {
  showForm: (formType: string) => void
  toggleTimeline: () => void
}

interface StateProps {
  balance: string | null
}

type FinalProps = OwnProps & StateProps

class TransactionListAppBarComponent extends React.Component<FinalProps> {
  showNewTransactionForm = () => {
    this.props.showForm('transaction')
  }

  showNewStatementForm = () => {
    this.props.showForm('statement')
  }

  showPlaidImportForm = () => {
    this.props.showForm('plaid')
  }
  showWaveImportForm = () => {
    this.props.showForm('wave')
  }

  toggleTimeline = () => {
    this.props.toggleTimeline()
  }

  render() {
    return (
      <AppBar position="static" color="default">
        <Toolbar>
          <Tooltip title="Timeline">
            <IconButton onClick={this.toggleTimeline}>
              <History />
            </IconButton>
          </Tooltip>
          {this.props.balance ? (
            <Typography
              variant="body1"
              key="balance"
            >{`Balance: $${this.props.balance}`}</Typography>
          ) : null}
          <div style={{ flex: 1 }} />
          <SearchBar />
          <IconMenu icon={<Add />}>
            <MenuItem key="new-transaction-menu" onClick={this.showNewTransactionForm}>
              <ListItemIcon>
                <AttachMoney />
              </ListItemIcon>
              <ListItemText>Transaction</ListItemText>
            </MenuItem>
            <MenuItem key="new-statement-menu" onClick={this.showNewStatementForm}>
              <ListItemIcon>
                <CreditCard />
              </ListItemIcon>
              <ListItemText>Statement</ListItemText>
            </MenuItem>
            <MenuItem key="new-wave-menu" onClick={this.showWaveImportForm}>
              <ListItemIcon>
                <Waves color="primary" />
              </ListItemIcon>
              <ListItemText>Wave Import</ListItemText>
            </MenuItem>
            <MenuItem key="new-plaid-menu" onClick={this.showPlaidImportForm}>
              <ListItemIcon>
                <GridOn color="primary" />
              </ListItemIcon>
              <ListItemText>Plaid Import</ListItemText>
            </MenuItem>
          </IconMenu>
          <IconButton
            onClick={() => {
              sessionStorage.removeItem('authToken')
              window.location.href = '/'
            }}
          >
            <Icon className="material-icons">exit_to_app</Icon>
          </IconButton>
        </Toolbar>
      </AppBar>
    )
  }
}
const mapStateToProps = (state: AppState, ownProps) => {
  return {
    balance: state.balance,
  }
}
export const TransactionListAppBar = connect<StateProps, {}>(
  mapStateToProps,
  () => ({}),
)(TransactionListAppBarComponent)
